import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import Specials from "../components/specials"
import GetInTouch from "../components/forms/get-in-touch"
import i18n from "../lib/i18n"
import { Translation } from "react-i18next"


import { graphql } from "gatsby"

export const pageQuery = graphql`
  query {
    allWpPromotion {
      edges {
        node {
          id
          title
          uri
          promotionFields {
            city
            headline
            headlineSpanish
            details
            detailsSpanish
            endDate
            image {
              sourceUrl
            }
            imageSpanish {
              sourceUrl
            }
          }
          featuredImage {
            node {
              sourceUrl
            }
          }
        }
      }
    }
  }
`

export default function SpecialsPage({ data }) {
  const specials = data.allWpPromotion.edges
  return (
    <Layout>
      <>{/* <pre>{JSON.stringify(specials, null, 2)}</pre> */}</>
      {i18n.language === "en" ? (
              <SEO title="Specials" />
            ) : (
              <SEO title="Especiales" />
            )}
      <Specials specials={specials} />
      <GetInTouch
        locationName="Air Rey A/C &amp; Heating"
        // address="5504 Bandera Rd. Suite 202"
        city="Serving Austin, Houston, Dallas, &amp; San Antonio"
        state=""
        zip=""
        phone="469-420-5155"
        email="sales@airreydfw.com"
      />
    </Layout>
  )
}
